@import url(https://fonts.googleapis.com/css?family=Lato:300);
@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);
#login {
  max-width: 700px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
}

#login h1 {
  font-weight: bold;
  font-size: 10rem;
  color: white;
  text-shadow: 0px 0px 50px rgba(255, 255, 255, 1);
}

#login p {
  color: white;
  line-height: 2;
  font-size: 1.5rem;
  padding: 2% 0;
}

.btn-light {
  margin: 2% 0;
  /* height: 50px; */
  font-size: 1.5rem;
  padding-top: 7px;
  color: #444;
  box-shadow: 0px 0px 21px 4px rgba(255,255,255,0.5);
}

.fa-spotify {
  color: #1ab26b;
}

.text-overflow {
overflow: hidden;
text-overflow: ellipsis;
white-space: nowrap;
width: 500px;
}
html,body,.background{width:100%;height:100%;margin:0;padding:0;font-family:"Lato",sans-serif}html{background:#fff}body{background:#eee}.background{background:#eee;background:linear-gradient(120deg, rgba(50, 150, 100, 0.2), rgba(0, 0, 100, 0))}.container{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:0;padding:0;height:100%;width:100%;overflow:auto;position:relative;background:#eee;background-color:#7c869f}#card{box-shadow:9px 7px 40px -6px rgba(0,0,0,.25);overflow:hidden;width:100%;padding:0;height:400px;min-height:300px;border-radius:5px;position:relative}#card .details{position:absolute;top:0;left:0;right:0;padding:16px 20px;color:#888;display:flex;flex-direction:row;justify-content:space-between;transition:color 2s ease}#card .details .right{text-align:right}#card .details #date{margin:4px 0}#card .details #summary{font-weight:600;font-size:22px}#card .details .temp{font-size:60px;line-height:60px}#card .details .temp span{font-size:18px;line-height:30px;vertical-align:top;margin-left:5px}.weather{background-color:#dae3fd;transition:background-color 2s ease}.weather #inner{background-color:#fff;background:linear-gradient(to bottom, rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 100%)}.weather .cloud{transition:fill 2s ease}.weather #cloud1{fill:#efefef}.weather #cloud2{fill:#e6e6e6}.weather #cloud3{fill:#d5d5d5}
.player {
    position: absolute;
    top: -3em;
    right: 25%;
    margin: auto; 
    width: 50%;
}

.playerControls div {
    display: inline;
    margin: 8%;
    /* padding: 30px; */
}
body .playlist-rows {
    text-align: left;
    font-size: 16px;
}

body .playlist-header {
    text-align: center;
    font-weight: bold;
    background-color: #444;
    color: white;
}
